.wrapper {
    min-height: 100vh;
    width: 100%;
    background: #222222;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ng_button {
    width: 200px;
    height: 35px;
    margin-bottom: 25px;
    cursor: pointer;
    color: white;
    outline: none;
    background: #222222;
    border: 1px solid white;
}

.ng_button:hover {
    background: #fff;
    color: #222222;
    transition: .3s all ease;
}

.game_info {
    margin-top: 25px;
    font-size: 35px;
    color: #fff;
}